* {
    /* border: 1px red solid; */
    box-sizing: border-box;
}

header {
    border-bottom: 1px black solid;
    width: 100%;
    height: 50px;
}

body {
    padding: 0px;
    margin: 0px;
}

#addCostDialogue {
    position: absolute;
    top: 15vh;
    left: 33vw;
    width: 50%;
    height: 20%;
    border: 1px solid black;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: aliceblue;
    #inputWrapper {
        display: flex;
        flex-direction: row;
        div {
            margin: 5px;
        }
    }
}

#container {
    display: flex;
    flex-direction: row;
}

#members {
    border: 1px black solid;
    border-radius: 5px;
    margin: 10px;
    padding: 10px;
    width: 25vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    // grid-template-columns: 1fr 1fr;
    // grid-template-rows: repeat(5, 30px);
    #memberWrap {
        width: 100%;
        border-top: 1px solid black;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    form {
        width:60%;
        #editMember {
            width: 70%;
        }
    }
    #header {
        grid-column: 1 / 3;
        font-weight: 800;
    }
    #add {
        grid-column: 1 / 3;
    }
}

#ledger {
    border: 1px solid black;
    border-radius: 5px;
    width: 75vw;
    height: 90vh;
    margin: 10px;
    padding: 10px;
    #ledgerHeader {
        display: grid;
        grid-template-columns: 1.5fr repeat(5, 1fr);
        justify-content: space-between;
    }
}

#names {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    width: 60%;
}

#monthYear {
    // width: 40%;
}

#ledgerHeader {
    font-weight: 800;
}

.ledgerSection {
    width: 100%;
    height: max-content;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin:0px;
}

.sectionHeader {
    font-weight: 800;
}

.costRow {
    display: grid;
    grid-template-columns: 1.5fr repeat(5, 1fr);
    gap: 2px;
    border-top: 1px black solid;
    span {
        padding: 3px;
    }
}

.amtInput {
    width: 100%;
}

.amounts {
    display: flex;
    flex-direction: column;
}

.totals {
    font-weight: 800;
}

.labels {
    display: flex;
    flex-direction: column;
}